.hero {
    height: 100vh;
    width: 100vw;
    background: url(https://github.com/Adalab/modulo-1-evaluacion-final-olguita2412/blob/main/src/images/header.jpg?raw=true);
    background-size: contain;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    &__title {
        font-size: 2.4rem;
        color: $color-darkgreen;
        font-weight: bold;
        text-align: center;
    }
    &__link {
        text-decoration: none;
        color: white;
        background-color: $color-darkgreen;
        width: 176px;
        height: 48px;
        text-align: center;
        font-size: 1.6rem;
        padding: 10px;
    }
    
    
}